.headerStyle {
	display: block;
	background-color: pink;
	height: 1000px;
	width: 100%;
}

.headerText {
	display: block;
	padding-top: 150px;
	padding-bottom: 135px;
	text-align: center;
}

.description {
	font-size: 24px;
	padding: 20px;
}

.sheildStyle {
	display: block;
	margin: auto;
}

.title {
	font-size: 100px;
	font-weight: bold;
}

@media (max-width: 768px) {
	.headerStyle {
		height: 850px;
	}
	
	.headerText {
		padding-top: 35px;
		padding-bottom: 10px;
	}
	
	.title {
		font-size: 50px;
	}
}
.containerStyle {
	display: flex;
	background-color: #5d49f4;
	width: 100%;
}

.imgContainer {
	width: 40%;
}

.imgStyle {
	display: block;
	height: 650px;
	margin: auto;
	padding-top: 65px;
}

.textContainer {
	display: block;
	margin-left: 8%;
	padding-top: 65px;
	padding-bottom: 40px;
	font-size: 40px;
	width: 35%;
}

@media (max-width: 768px) {
	.containerStyle {
		height: 100%;
	}
	
	.imgContainer {
		width: 0%;
	}
	
	.imgStyle {
		height: 180px;
		padding-top: 60px;
		padding-left: 16px;
	}
	
	.textContainer {
		font-size: 20px;
		padding-top: 60px;
		padding-bottom: 40px;
		margin-left: 40%;
		width: 50%;
	}
}
.templateStyle {
	display: flex;
	margin: auto;
	width: 70%;
	border: solid;
}

.nameBox {
	width: 25%;
	border-right: solid;
	text-align: center;
}

.nameStyle {
	font-size: 30px;
}

.powersBox {
	width: 75%;
}

@media (max-width: 768px) {
	.templateStyle {
		display: block;
		width: 95%;
	}
	
	.nameBox {
		width: 95%;
		border-right: none;
	}
	
	.nameStyle {
		text-align: center;
		font-size: 40px;
	}
	
	.powersBox {
		width: 85%;
	}
}
.abilitysStyle {
	background-color: #fff852;
}

.title {
	text-align: center;
	margin: 0px;
	padding: 0px;
	font-size: 70px;
}

@media (max-width: 768px) {
	.title {
		text-align: center;
		margin: 0px;
		padding-top: 20px;
		padding-bottom: 20px;
		font-size: 50px;
	}
}
