.templateStyle {
	display: flex;
	margin: auto;
	width: 70%;
	border: solid;
}

.nameBox {
	width: 25%;
	border-right: solid;
	text-align: center;
}

.nameStyle {
	font-size: 30px;
}

.powersBox {
	width: 75%;
}

@media (max-width: 768px) {
	.templateStyle {
		display: block;
		width: 95%;
	}
	
	.nameBox {
		width: 95%;
		border-right: none;
	}
	
	.nameStyle {
		text-align: center;
		font-size: 40px;
	}
	
	.powersBox {
		width: 85%;
	}
}