.headerStyle {
	display: block;
	background-color: pink;
	height: 1000px;
	width: 100%;
}

.headerText {
	display: block;
	padding-top: 150px;
	padding-bottom: 135px;
	text-align: center;
}

.description {
	font-size: 24px;
	padding: 20px;
}

.sheildStyle {
	display: block;
	margin: auto;
}

.title {
	font-size: 100px;
	font-weight: bold;
}

@media (max-width: 768px) {
	.headerStyle {
		height: 850px;
	}
	
	.headerText {
		padding-top: 35px;
		padding-bottom: 10px;
	}
	
	.title {
		font-size: 50px;
	}
}