.containerStyle {
	display: flex;
	background-color: #5d49f4;
	width: 100%;
}

.imgContainer {
	width: 40%;
}

.imgStyle {
	display: block;
	height: 650px;
	margin: auto;
	padding-top: 65px;
}

.textContainer {
	display: block;
	margin-left: 8%;
	padding-top: 65px;
	padding-bottom: 40px;
	font-size: 40px;
	width: 35%;
}

@media (max-width: 768px) {
	.containerStyle {
		height: 100%;
	}
	
	.imgContainer {
		width: 0%;
	}
	
	.imgStyle {
		height: 180px;
		padding-top: 60px;
		padding-left: 16px;
	}
	
	.textContainer {
		font-size: 20px;
		padding-top: 60px;
		padding-bottom: 40px;
		margin-left: 40%;
		width: 50%;
	}
}